// Show and hide forms
$(".hide-form").slideUp(0);
$("[data-open]").click(function(){
    $($(this).attr("data-open")).slideDown(800);
});

// Validate comment form
let nameInput = $("#name");
let bodyInput = $("#body");
$(".comment-form").submit((e)=>{

    let isValid = true;    // The form is valid indecator

    // Trim the values
    nameInput.val(nameInput.val().trim());
    bodyInput.val(bodyInput.val().trim());

    if(!(nameInput.val() == '' || nameInput.val().length >= 3)){
        isValid = false;
        nameInput.addClass('is-invalid').next().text('The name could only be empty of more than 3 characters long');
    } else {
        nameInput.removeClass('is-invalid');
    }
    
    if(bodyInput.val().length < 2){
        bodyInput.addClass('is-invalid').next().text("The comment can't be less thant 2 characters long");
        isValid = false;
    } else {
        bodyInput.removeClass('is-invalid');
    }

    if(!isValid){
        e.preventDefault();
    }
    return isValid;
});


// Validate "create event form"
let titleInput = $("#title");
let descInput = $("#description");
let latInput = $("#lat");
let lngInput = $("#lng");
let dateInput = $("#date");
$("form.create-event-form").submit((e)=>{

    let isValid = true;

    // Trim the values
    titleInput.val(titleInput.val().trim());
    descInput.val(descInput.val().trim());
    latInput.val(latInput.val().trim());
    lngInput.val(lngInput.val().trim());
    dateInput.val(dateInput.val().trim());

    // The title
    if(titleInput.val().length < 5){
        titleInput.addClass('is-invalid').next().text("The title can't be less than 5 characters long");
        isValid = false;
    } else {
        titleInput.removeClass('is-invalid');
    }

    // The description
    if(descInput.val().length < 3){
        descInput.addClass('is-invalid').next().text("The description can't be less than 3 characters long");
        isValid = false;
    } else {
        descInput.removeClass('is-invalid');
    }
    
    // The lat
    if(latInput.val() == '' || isNaN(latInput.val())){
        latInput.addClass('is-invalid').next().text("Invalid latitude value");
        isValid = false;
    } else {
        latInput.removeClass('is-invalid');
    }
    // The lng
    if(lngInput.val() == '' || isNaN(lngInput.val())){
        lngInput.addClass('is-invalid').next().text("Invalid longitude value");
        isValid = false;
    } else {
        lngInput.removeClass('is-invalid');
    }

    if(!isValid){
        e.preventDefault();
    }

    return isValid;
});


// Validate search form
let queryInput = $("#query");
$(".nav-form").submit((e)=>{
    let isValid = true;

    queryInput.val(queryInput.val().trim());

    if(queryInput.val().trim() == ''){
        isValid = false;
    }


    if(!isValid){
        e.preventDefault();
    }
    return isValid;
})

